.feedback {
  margin: 4rem 0;
}

.feedback_container {
  max-width: 800px;
  margin: 0 auto;
}

.feedback_container .title-container h2 {
  color: #27AF00;
  letter-spacing: 1px;
  font-size: 24px;
  list-style: 34px;
  font-weight: 700;
}

.feedback_container .tns-outer {
  position: relative;
  margin-top: 2rem;
}

.feedback_container .tns-controls {
  display: none;
}

.feedback_container .tns-controls button {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  background-color: #27AF00;
  color: #fff;
  font-weight: 900;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.5s ease;
}

.feedback_container .tns-controls button:after {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 12px;
  height: 12px;
  border-radius: 3px;
  border-top: 3px solid #fff;
  border-right: 3px solid #fff;
}

.feedback_container .tns-controls button:first-child {
  left: -5.375rem;
}

.feedback_container .tns-controls button:first-child::after {
  transform: rotate(-135deg) translateX(-1px);
}

.feedback_container .tns-controls button:last-child {
  right: -5.375rem;
}

.feedback_container .tns-controls button:last-child::after {
  transform: rotate(45deg) translateY(1.5px);
}

.feedback_container .tns-controls button:hover {
  transform: translateY(-50%) scale(0.9);
}

.feedback_container .tns-nav {
  display: -ms-flexbox;
  display: flex;
  gap: 0.5rem;
  margin-top: 3rem;
}

.feedback_container .tns-nav button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #A0A3BD;
  border: none;
  transition: width 0.5s ease, background-color 0.5s ease;
}

.feedback_container .tns-nav button.tns-nav-active {
  width: 16px;
  background-color: #004936;
  border-radius: 4px;
  transition: width 0.5s ease, background-color 0.5s ease;
}

.feedback-slider-container--item {
  border-radius: 32px;
  background: #F4F4F4;
  padding: 3rem;
  box-sizing: border-box;
}

.feedback-slider-container--item .text-container {
  margin: 1.5rem 0;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 0.75px;
  color: #004936;
}

.feedback-slider-container--item .person-container h3 {
  color: #004936;
  letter-spacing: 0.75px;
  font-weight: 600;
  font-size: 1rem;
}

.feedback-slider-container--item .person-container span {
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.75px;
  color: #27AF00;
}

@media (min-width: 992px) {
  .feedback_container .title-container h2 {
    font-size: 2.5rem;
    line-height: 48px;
  }
  .feedback-slider--item .text-container {
    font-size: 1.25rem;
    line-height: 32px;
  }
  .feedback-slider--item .person-container h3 {
    font-size: 20px;
    line-height: 32px;
  }
}
@media (min-width: 1232px) {
  .feedback_container .tns-controls {
    display: block;
  }
}